<template>
  <header class="app-header">
    <div class="header-img-box">
      <router-link to="/mission-note">
        <img :class="['header-img', {'header-animate': getShakeItem === 'note'}]" src='@/assets/images/note.png' alt='note'>
      </router-link>
      <router-link to="/monster-compass">
        <img :class="['header-img', {'header-animate': getShakeItem === 'compass'}]" src='@/assets/images/compass.png' alt='compass'>
      </router-link>
      <router-link to="/monster-collect">
        <img :class="['header-img', {'header-animate': getShakeItem === 'collect'}]" src='@/assets/images/monster-collect.png' alt='monster-collect'>
      </router-link>
    </div>
  </header>
</template>
<script>

export default {
  name: 'AppHeader',
  components: {

  },
  data () {
    return {

    }
  },
  mounted () {

  },
  destroyed () {

  },
  methods: {
    shakeNote () {
      this.$store.commit('shakeHeader', 'note')
      setTimeout(() => {
        this.$store.commit('shakeHeader', '')
      }, 1000)
    }
  },
  computed: {
    getShakeItem () {
      return this.$store.state.shakeHeaderItem
    }
  }
}
</script>
<style lang="scss" scoped>
.app-header {
  position: fixed;
  top: 20px;
  left: 0px;
  width: 100%;
  z-index: 1;

  .header {

    &-img-box {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 320px;
      margin: auto;
    }

    &-img {
      width: 50px;
    }

    &-animate {
      animation-name: shakeBox;
      animation-duration: 600ms;
      animation-direction: alternate;
    }
  }
}

@keyframes shakeBox {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(360deg);
  }

  50% {
    transform: rotate(0deg);
  }

  75% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(0deg);
  }

}

</style>



