<template>
  <footer class="app-footer">
    <div class="footer-box">
      <div class="footer-text">能力值</div>
      <div class="footer-power">
        <div class="footer-power-data" :style="`width: ${power}%`"></div>
      </div>
    </div>
  </footer>
</template>
<script>

export default {
  name: 'AppFooter',
  components: {

  },
  data () {
    return {

    }
  },
  computed: {
    power () {
      return this.$store.state.power
    }
  }
}
</script>
<style lang="scss" scoped>
.app-footer {
    position: fixed;
    bottom: 30px;
    left: 46%;

  .footer {

    &-box {
      display: flex;
    }

    &-text {
      background: #000;
      border: 2px solid #000;
      width: 48px;
      height: 30px;
      color: #95ff00;
      font-size: 12px;
      text-align: center;
      line-height: 26px;
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;
    }

    &-power {
      border: 2px solid #000;
      height: 30px;
      width: 120px;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
      background: #000;
    }

    &-power-data {
      height: 100%;
      background:  #95ff00;
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;
    }
  }

}

</style>



