import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  power: 80,
  shakeHeaderItem: '',
  getLingCard: '',
  isFight: {
    lingStatus: ''
  },
  checkoutPoint: ''
}

const mutations = {
  changePoint (state, data) {
    state.checkoutPoint = data
  },
  fightLing (state, data) {
    state.isFight.lingStatus = data
  },
  minusPower (state) {
    state.power--
  },
  shakeHeader (state, data) {
    state.shakeHeaderItem = data
  },
  getLingCard (state, data) {
    state.getLingCard = data
  }
}

const actions = {

}

export default new Vuex.Store({
  state,
  mutations,
  actions
})



